
import {mapGetters} from 'vuex'
import Toast from 'vux/src/components/toast/index.vue'


export default {
computed: mapGetters([]),
components: {
Toast
},
data() {
return {
VerificationCodeValue: '验证码',
isVerificationCode: true,
isNum: false,
num: 60,
type: 'warn',
timer: null,
toastValue: '手机号不能为空！',
showPositionValue: false,
register: {
"loginUserID": "huileyou",
"loginUserPass": "123",
"phone": "",
"extensionID": "",
"password": "",
"validateNo": "",//验证码
}
}
},
watch: {
num(){
if (this.num == 0) {
clearInterval(this.timer)
this.VerificationCodeValue = '重新发送验证码'
this.isVerificationCode = true;
this.isNum = false;
}
}
},
methods: {

VerificationCode(){
if (!this.register.phone) {
this.showPositionValue = true;
return
}
let options = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"phone": this.register.phone,//用户编码
"sendType": "0",//0注册发送短信 1 找回密码发送短信 2 动态密码
};
this.$store.dispatch('VerificationCode', options)
.then((data) => {

this.type = 'success';
this.toastValue = data
this.showPositionValue = true;


this.isVerificationCode = false;
this.isNum = true

clearInterval(this.timer)
this.timer = setInterval(() => {
this.num--;
}, 1000);
}, err => {
this.type = 'warn';
this.toastValue = err
this.showPositionValue = true;
})
},

submit(){
if (!this.register.validateNo) {
this.toastValue = '验证码不能为空'
this.showPositionValue = true;
return
}
if (!this.register.password) {
this.toastValue = '密码不能为空'
this.showPositionValue = true;
return
}
this.$store.dispatch('registerSubmit',this.register)
.then((suc)=>{

this.type = 'success';
this.toastValue = suc
this.showPositionValue = true;
this.$router.push({name: 'Login'})
}, err => {
this.type = 'warn';
this.toastValue = err
this.showPositionValue = true;
})
},
goLogin(){
this.$router.push({name: 'Login'})
},
initData() {
},
search() {
this.initData()
}
},
}



